.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    transition: top 0.3s;
}

.hidden {
    display: none;
}

.nav {
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
    backdrop-filter: blur(5px);
    padding: 0 2rem;
}

.nav__logo {
    z-index: var(--z-fixed);
}

.logo {
    max-height: 30px;
}

.nav__toggle {
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav__list {
    display: flex;
    column-gap: 2rem;
}

.nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--normal-font-size);    
    color: var(--title-color);
    font-weight: var(--font-bold);
    transition: .3s;
}

.nav__icon,
.nav__close,
.nav__toggle {
    display: none;
}

.nav__link:hover {
    color: var(--link-color-hover);
}

/* for medium devices */
@media screen and (max-width: 768px) {
    .header {
        top: initial; 
        bottom: 0; 
        backdrop-filter: blur(0)
    }

    .nav__menu {
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: .3s;
        background: rgba(255,255,255,1);
    }

    /* Show Menu */
    .show-menu {
        bottom: 0;
        padding: 2rem 1.5rem 4rem;
    }

    .show-menu .nav__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }

    .nav__icon {
        font-size: 1.2rem;
    }

    .nav__icon, .nav__toggle {
        display: block;
        z-index: var(--z-fixed);
    }

    .nav__toggle {
        font-size: 1.5rem;
        cursor: pointer;
    }
}

@media screen and (max-width: 768px) {
    .header {
        display: none;
    }
}