.image {
    max-width: 35rem;
    border-radius: 2rem;
}

img {
    object-fit: cover;
}

.about__line {
    color: #33BBCA;
    padding-right: 5%;
}

.about {
    padding-top: 6rem;
}

.about__intro {
    background: #fff9e9;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    line-height: 90%;
    font-size: small;
}

.location, .relax, .classroom {
    max-width: 35px;
}

.about__elems {
    justify-content: space-evenly;
}

.about__container {
    grid-template-columns: repeat(2, 1fr);
    grid-template-columns: 55% 35%;
    gap: 10%;
    max-width: 900px;
    padding: 0 0 4.5rem 0;
}

.about__horizontal {
    margin: 1em auto; 
    min-width: 45px;
    max-width: 65px;
    border: 1px solid rgb(188, 185, 185); 
    border-radius: 1em; 
    background-color: rgb(191, 188, 188);
}

.about__description {
    padding: 0 0 2rem 0;
    text-align: justify;
}

/* large devices */

@media screen and (max-width: 968px) {
    .about__container {
      max-width: 650px;
    }

    .image {
        max-width: 25rem;
        border-radius: 1rem;
    }
}

/* medium devices */
@media screen and (max-width: 768px) {
    .about {
        padding: 3rem 0 0 0;
    }

    .about__container {
        grid-template-columns: 1fr;
        padding: 0 0 2rem 0;
    }

    .image {
        max-width: 450px;
        object-fit: cover;
    }

    .about__intro {
        padding: 2rem 0 2rem 0;
        margin-bottom: 3rem;
    }
}

@media screen and (max-width: 576px) {
    .image {
        max-width: 250px;
        object-fit: cover;
    }

    .about__intro {
        padding: 1rem 1rem 1rem 1rem;
        margin-bottom: 2rem;
    }
}

/* small devices */
@media screen and (max-width: 350px) {
    .image {
        max-width: 180px;
        object-fit: cover;
    }
}