.contact__line {
    color: #FE7A15;;
    padding-right: 12%;
}

.btn {
    font-size: 16px;
}

.buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 350px;
    flex-direction: column;
    margin: 0 auto;
}

.telephone, .whatsapp {
    max-height: 20px;
    align-self: baseline;
    margin-left: 5px;
}

.btn {
  appearance: button;
  border: solid transparent;
  border-radius: 16px;
  border-width: 0 0 4px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-family: din-round,sans-serif;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: .8px;
  line-height: 20px;
  margin: 0;
  outline: none;
  overflow: visible;
  padding: 13px 16px;
  text-align: center;
  text-transform: uppercase;
  touch-action: manipulation;
  transform: translateZ(0);
  transition: filter .2s;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  width: 100%;
}

.btn:after {
  background-clip: padding-box;
  border: solid transparent;
  border-radius: 16px;
  border-width: 0 0 4px;
  bottom: -4px;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}

.btn:main,
.btn:focus {
  user-select: auto;
}

.btn:hover:not(:disabled) {
  filter: brightness(1.1);
  -webkit-filter: brightness(1.1);
}

.btn:disabled {
  cursor: auto;
}
.whatsapp_button {
    background-color: #25D366;
    margin-bottom: 4rem;
}

.whatsapp_button:after {
    background-color: #3cf07e;
}

.phone_button {
    background-color: #1899D6;   
}

.phone_button:after {
    background-color: #1CB0F6;
}



table { 
  border-spacing: 1; 
  border-collapse: collapse; 
  background: #fff9e9;
  border-radius:6px;
  overflow:hidden;
  max-width:800px; 
  width:100%;
  margin:4rem auto;
  position:relative;
}
  
td,th { padding-left:8px}

thead tr { 
    height:60px;
    background:#FFED86;
    font-size:16px;
}
  
tbody tr { height:48px; border-bottom:1px solid #E3F1D5 ;
    &:last-child  { border:0; }
}
  
td,th { text-align:left;
&.l { text-align:right }
&.c { text-align:center }
&.r { text-align:center }
}



@media screen and (max-width: 35.5em) {
table { display:block;}
thead { display:none }
tbody tr { height:auto; padding:8px 0;}
td { padding-left:45%; margin-bottom:12px;
        &:last-child          { margin-bottom:0 }
        &:before              { 
          position:absolute;
          font-weight:700;
          width:40%;
          left:10px;
          top:0
        }
        
        &:nth-child(1):before { content:"Курс";}
        &:nth-child(2):before { content:"Группа";}
        &:nth-child(3):before { content:"Статус";}
        &:nth-child(4):before { content:"Начало занятий";}
      }        
    }
  
@media screen and (max-width: 576px) {
  .contact {
    padding-top: 6rem;
  }

  .buttons {
    max-width: 250px;
  }

  .telephone, .whatsapp {
      max-height: 15px;
      margin-left: 3px;
  }

  .btn {
    border-radius: 12px;
    border-width: 0 0 3px;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: .6px;
    line-height: 15px;
    
    padding: 11px 13px;
  }
}

@media screen and (max-width: 350px) {
  .buttons {
    max-width: 180px;
  }

  .telephone, .whatsapp {
      max-height: 12px;
      margin-left: 2px;
  }

  .btn {
    border-radius: 9px;
    border-width: 0 0 2px;
    font-size: 10px;
    font-weight: 700;
    letter-spacing: .5px;
    line-height: 12px;
    
    padding: 10px 12px;
  }
}