* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}

*{
  font-family: var(--body-font);
  color: var(--title-color);
  font-weight: 700;
}

/*=============== VARIABLES CSS ===============*/
:root {
  --header-height: 3rem;

  /*========== Colors ==========*/
  /*Color mode HSL(hue, saturation, lightness)*/
  --hue: 0;
  --sat: 0%;
  --title-color: hsl(var(--hue), var(--sat), 20%);
  --title-color-dark: hsl(var(--hue), var(--sat), 0%);
  --text-color: hsl(var(--hue), var(--sat), 35%);
  --body-color: #f4dbdf;
  --container-color: #fff;
  --body-font: "Advent Pro";
  --header-font: Neucha;
  --link-color: #f5a623;
  --link-color-hover: #ffce7d;


  /*.5rem = 8px | 1rem = 16px ...*/
  --big-font-size: 2.75rem;
  --h1-font-size: 2rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;

  /*========== Font weight ==========*/
  --font-normal: 400;
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margenes Bottom ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

/* Responsive typography */
@media screen and (max-width: 992px) {
  :root {
    --big-font-size: 2.75rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.2rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

/*=============== BASE ===============*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  letter-spacing: 0.15ex;

}

body {
  background: var(--body-color);
  color: var(--text-color);
}

h1,
h2,
h3 {
  font-family: var(--header-font);
  color: var(--title-color);
  font-weight: var(--font-semi-bold);

}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

/*=============== REUSABLE CSS CLASSES ===============*/
.section {
  padding: 2rem 0 2rem;
}

.section__title {
  font-size: var(--h1-font-size);
  color: var(--title-color);
}

.letterspacing {
  letter-spacing: -4px;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 0.67em;
}

.section__subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: 4rem;
}

.section__title,
.section__subtitle {
  text-align: center;
}

/*=============== LAYOUT ===============*/
.container {
  max-width: 968px;
  margin-left: auto;
  margin-right: auto;
}

.main {
  box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.15),
            4px 8px 16px 0px rgba(0,0,0,0.15),
            8px 16px 32px 0px rgba(0,0,0,0.15),
            8px 16px 64px 0px rgba(0,0,0,0.15);
  border-radius: 0% 0% 2rem 2rem;
  background-color: var(--container-color);
  margin-bottom: 4rem;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

/*=============== BUTTONS ===============*/
.button {
  display: inline-block;
  background-color: var(--link-color);
  color: var(--container-color);
  padding: 1.25rem 2rem;
  border-radius: 1rem;
  font-weight: var(--font-medium);
}

.button:hover {
  background-color: var(--link-color-hover);
}

.button__icon {
  margin-left: var(--mb-0-5);
}

.button--flex {
  display: inline-flex;
  align-items: center;
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
  .container {
    margin-left: var(--mb-1-5);
    margin-right: var(--mb-1-5);
  }

  .button {
    padding: 1rem 1.75rem;
  }

  .button__icon {
    width: 22px;
    height: 22px;
  }
}

/* For medium devices */
@media screen and (max-width: 768px) {
  body {
    margin: 0 0 var(--header-height) 0;
  }

  .section {
    padding: 2rem 0 4rem;
  }

  .section__subtitle {
    margin-bottom: var(--mb-3);
  }
}

@media screen and (max-width: 576px) {
}

/* For small devices */
@media screen and (max-width: 350px) {
  :root {
    --big-font-size: 2.25rem;
  }

  .container {
    margin-left: var(--mb-1);
    margin-right: var(--mb-1);
  }
}

.icon {
  max-height: 65px;
}

.icon__container {
  width: 50%;
  max-width: 600px;
  max-height: 600px;
  background-color: #fff;
  border-radius: 1rem;
}

.flex {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  margin: 0 auto;
}

.flex-reverse {
  display: flex;
  flex-direction: row-reverse;
  gap: 1rem;
  margin: 0 auto;
}

.elem {
  scale: 0.4;
}

.wrapper {
  max-width: 900px;
  margin: 0 auto;
}

.elems__container {
  display: grid;
  grid-auto-flow: column;
  justify-content: space-evenly;
}

@media screen and (max-width: 968px) {
  .wrapper {
    max-width: 650px;
  }
}

@media screen and (max-width: 768px) {
  .elem {
    scale: 0.3;
  }

  .wrapper {
    max-width: 450px;
  }
}

@media screen and (max-width: 576px) {
  .elem {
    scale: 0.2;
  }

  .wrapper {
    max-width: 250px;
  }

  .elems__container {
    display: grid;
    grid-auto-flow: row;
    grid-auto-rows: 90px;  
  }

  .courses__elems {
    padding: 3rem 0 0 0;
  }

  .section {
    padding: 2rem 0 0rem;
  }
}

@media screen and (max-width: 350px) {
  .wrapper {
    max-width: 180px
  }

  .elems__container {
    margin-left: 5rem;
  }
}