.courses__container {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: row;
    gap: 2rem;
    padding: 2rem 0 0 0 ;
}

.courses__description {
    grid-column: 1
}

.icon {
    grid-column: 2;
    width: 75px;
    align-self: center;
}

.icon__container {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

.courses__line {
    color: #56C596;
    padding-left: 5%;
}

.english__title {
    background: linear-gradient(to right, #56cbb0, #438a78);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.container__english {
    background-color: rgba(134,227,206, 0.3);
}

.russian__title {
    background: linear-gradient(to right, #ffdd94, #e2bc69);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.container__russian {
    background-color: rgba(255,221,148, 0.3)
}

.programming__title {
    background: linear-gradient(to right, #a7d676, #63bc5c);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.container__programming {
    background-color: rgba(208,230,165, 0.3);
}

.math__title {
    background: linear-gradient(to right, #ccabd8, #806cac);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.container__math {
    background-color: rgba(204,171,216, 0.3)
}

.course__title {
    font-size: 20px;
}

.course__number {
    font-family: var(--header-font);
    font-weight: 600;
    font-size: 28px;
}

.courses__description {
    text-align: justify;
    max-width: 500px;
}

.courses__elems {
    padding: 3rem 0 0 0;
}

/* medium devices */
@media screen and (max-width: 768px) {
    .courses__elems {
        padding: 0 0 0 0;
    }

    .courses__container {
        grid-template-columns: 1fr;
        grid-auto-flow: column;
    }
}

@media screen and (max-width: 576px) {
    .icon__container {
        display: none;
    }

    .course__number {
        display: table;
        margin: 0 auto;
    }

    .course__title {
        text-align: center;
    }

    .courses__elems {
        padding: 1rem 0 0 0;
        grid-auto-rows: 70px;  
    }
}